@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
:root {
  --primary-white: #ffffff;
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52, 58, 64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-hover-brown: #654321;
  --primary-light-gray: #f4f4f4;
  --primary-gray: #ccd1d9;
  --primary-dark-gray: #666666;
  --primary-black: #000000;
  --primary-green: green;
}
body {
  font-family: "Roboto Condensed", sans-serif !important;
}
/*************************NAVBAR*****************************/
nav {
  z-index: 2;
  height: 4.5rem;
}
.navbar-logo {
  width: 15rem;
}
/*************************NAV LINKS**************************/
nav a.nav-link {
  font-weight: 500;
  letter-spacing: 0.0375rem;
  cursor: rem;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  color: #ffffff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgb(52, 58, 64);
  border-top: 0.1875rem solid var(--primary-dark);
}
@media (max-width: 768px) {
  .navbar-brand{
    width: 7rem;
  }
  #navbarSupportedContent{
    background: rgb(52, 58, 64);
    background: var(--primary-dark);
  }
}

/* can delete the below segment */
.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid #f9ab00;
  border-top: 0.1875rem solid var(--primary-orange);
}

/*************************HEADER*****************************/
.header-wrapper {
  position: relative;
  background: url(/my-portfolio-page/static/media/background.99414195.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
  color: #f9ab00;
  color: var(--primary-orange);
}

/*************************TYPED TEXT*****************************/
.typed-text {
  font-size: 2rem;
  color: #ffffff;
  color: var(--primary-white);
}
@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

/*************************HEADER MAIN OFFER BUTTON***************/

.btn-main-offer {
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0%;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #ffffff;
  color: var(--primary-white);
  margin: 2rem;
  font-weight: bold;
  background-color: #666666;
  background-color: var(--primary-dark-gray);
  background: none;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  transition: 0.2s ease-in-out;
  color: #000000;
  color: var(--primary-black);
  font-weight: bold;
}
/*************************PARTICLES JS**************************/

.tsparticles-canvas-el {
  position: absolute;
  height: 10vh;
  width: 100 vw;
  z-index: 1;
}
 @media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
} 

/*************************ABOUT ME**************************/
.photo-wrap{
  padding-top: 4rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.profile-img {
  width: 30.125rem;
  height: 24.125rem;
  border-radius:  50%;
  padding: .3rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
}

.about-heading{
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

@media(max-width: 468px) {
  .profile-img{
    padding: 0rem;
    width: 20rem;
    height: 15rem;
  }
}
/*************************SKILLSETS**************************/

.skillsets{
  text-align: center;
  padding-bottom: 2rem;
  background: #f4f4f4;
  background: var(--primary-light-gray);
}

.skillsets h1{
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
  text-transform: uppercase;
}

.skillsets .circle{
  position: relative;
  margin: 0.375rem auto;
  background: #f9ab00;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.skillsets .icon{
  font-size: 1.4rem;
  color: #ffffff;
  color: var(--primary-white);
  position: absolute;
  top:50%;
  left:50%;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
}
.skillsets .box{
  height: 16rem;
  border-bottom: 0.3125rem solid #f9ab00;
  border-bottom: 0.3125rem solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #ffffff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}
.skillsets .box:hover{
  background: #f9ab00;
  background: var(--primary-orange) ;
  border-bottom:  0.3125rem solid rgb(52, 58, 64);
  border-bottom:  0.3125rem solid var(--primary-dark);
}
.skillsets .box:hover .icon {
 color: rgb(52, 58, 64);
 color: var(--primary-dark) ;
}
.skillsets .box:hover .circle {
  background: #ffffff;
  background: var(--primary-white);
}

.skillsets .box:hover p {
  color: #ffffff;
  color: var(--primary-white);
}

/*************************EXPEREIENCE**************************/

.experience {
  padding: 1rem;
}
.experience h1{
  text-transform: uppercase;
  color: #f9ab00;
  color: var(--primary-orange);
  
}

.experience-wrapper{
  position: relative;
  padding: 3.125rem;
  overflow: hidden;
}

.experience-wrapper:before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: #ccd1d9;
  background: var(--primary-gray);
  z-index: 1;
}

.timeline-block{
  width: calc(50% + 0.5rem);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  clear: both;
}

.timeline-block-right{
  float:right;
}
.timeline-block-left{
  float:left;
  direction: rtl;
}
.marker{
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border:0.3125rem solid rgb(52, 58, 64);
  border:0.3125rem solid var(--primary-dark);
  background: #f9ab00;
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}
.timeline-content{
  width: 95%;
  padding: 0 0.9375rem;
  color: rgb(52, 58, 64);
  color: var(--primary-dark);
  direction: ltr;
}
.timeline-content h3{
  margin-top: 0.3125rem;
  color: #ef4035;
  color: var(--primary-red);
  font-weight: 900;
}
/*----------------------MEDIA------------------------*/

@media (max-width: 768px){
  .experience .container{
    padding-left: 0;
  }
  .experience-wrapper::before{
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right{
    float: none;
    width: 100%;
  }
  .timeline-block-left{
    float: none;
    direction: ltr;
    width: 100%;
  }
  .experience h1{
    text-align: center;
  }
}

/*************************PORTFOLIO**************************/
.portfolio-wrapper{
  background: #f4f4f4;
  background: var(--primary-light-gray);
  padding-bottom: 5rem;
  padding-top: 1.5rem;
}


.portfolio-image {
  width: 15rem;
  border: 1px solid #f9ab00;
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
}

.portfolio-image-box{
  position: relative;
  margin: 1rem;
 
}
.popupbox-wrapper{
  margin-top: 5rem;
}

/*************************OVERFLOW BOX**************************/
.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  height: 8rem;
  top:0;
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red);
  cursor: pointer;
}

.portfolio-image-box:hover .overflow{
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon{
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: #f9ab00;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon{
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

/*************************OVERFLOW BOX**************************/
.portfolio-image-popupbox{
  width: 35rem;
  padding: 0 0.5rem;
}

.hyper-link{
  cursor: pointer;
  color: #f9ab00;
  color: var(--primary-orange);

}
.hyper-link:hover{
  color: #ef4035;
  color: var(--primary-red);
  
}

/***********************MEDIA***********************************/

@media(max-width:768px){
  .portfolio-image-popupbox{
    width:100%;
  }
}

@media(max-height:570px){
  .popupbox-wrapper{
    width: 100%
  }
  .portfolio-image-popupbox{
    width: 50%
  }

}

/**********************CONTACTS**************************/
.contacts{
  padding: 3rem 0;
}

.contacts h1{
  color: #f9ab00;
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform: uppercase;
}

.contacts p{
  font-size: 1.2rem;
}
/*************************INPUTS**************************/
.contacts input, .contacts textarea{
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid #ccd1d9;
  border-bottom: 0.125rem solid var(--primary-gray);
  border-radius: 0;
  box-shadow: none !important;
  margin-bottom: 1rem;
  padding-left: 0 !important;
  outline: 0 !important;
  top: 0;
}

.contacts .container{
  padding: 4rem 0;
}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  border-bottom: 0.125rem solid #ccd1d9;
  border-bottom: 0.125rem solid var(--primary-gray);
}

.contacts textarea{
  height: 12.2rem !important;

}
.contact-btn{
  margin: 1rem 0 0 0 ;
  width: 100%;
  color: #666666;
  color: var(--primary-dark-gray);
}

.contact-btn:hover{
  color: #ffffff;
  color: var(--primary-white);
}

.line{
  position: relative;
  top:-0.8rem;
  width: 0;
  height: 0.125rem;
  background: #f9ab00;
  background: var(--primary-orange);
  display: inline-block
}
input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control{
  margin: -0.3rem 0;
}

textarea.form-control{
  margin: -0.3rem 0;
}
.error-message{
  color: #ffffff;
  color: var(--primary-white);
  background: #ef4035;
  background: var(--primary-red);
  text-align: left;
}
.success-message{
  font-weight: 900;
  color: green;
  color: var(--primary-green);
}
/************************MEDIA******************************/

@media(max-width: 768px){
  .contacts {
    overflow-x: hidden;
  }
  .contacts .container{
    padding: 0 1rem;
  }
  .contacts p{
    padding: 0 0.4rem;
  }
}
/************************FOOTER******************************/
.footer{
  background: #000000;
  background: var(--primary-black);
  color: #666666;
  color: var(--primary-dark-gray);
  padding: 3rem;

}
.footer a{
  text-decoration: none;
  color: #666666;
  color: var(--primary-dark-gray);

}

.footer a:hover{
  text-decoration: none;
  color: #f9ab00;
  color: var(--primary-orange);
  cursor: pointer;
}

.footer p{
  margin-bottom: 0;
}
.footer-logo {
  width: 15rem;
}
